<template>
  <div>
    <city-header/>
    <city-search/>
    <city-list/>
    <city-alphabet/>
  </div>
</template>

<script>
import CityHeader from '@/views/city/Header'
import CitySearch from '@/views/city/Search'
import CityList from '@/views/city/List'
import CityAlphabet from '@/views/city/Alphabet'

export default {
  name: 'City',
  components: {
    CityHeader,
    CitySearch,
    CityList,
    CityAlphabet
  },
  mounted () {
    this.$store.dispatch('getCityData')
  }
}
</script>

<style lang="stylus" scoped>

</style>



