<template>
  <div class="container" @click="handleGallaryClick">
    <div class="wrapper">
      <swiper :options="swiperOption">
        <swiper-slide>
          <img class="gallary-img" src="http://img1.qunarzz.com/sight/p0/201301/11/a98fb72963b600b293835fbb.png_r_800x800_b5e63661.png"/>
        </swiper-slide>
        <swiper-slide>
          <img class="gallary-img" src="http://img1.qunarzz.com/sight/p0/201405/22/d9b0e37b5118004e3f08dd723c586019.jpg_r_800x800_87d22113.jpg"/>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        observeParents: true,
        observer: true
      }
    }
  },
  methods: {
    handleGallaryClick () {
      this.$emit('close')
    }
  }
}
</script>

<style lang='stylus' scoped>
.container >>> .swiper-container
  overflow inherit
.container
  display flex
  flex-direction column
  justify-content center
  z-index 99
  position fixed
  left 0
  right 0
  top 0
  bottom 0
  background #000
  .wrapper
    width 100%
    height 0
    padding-bottom 66.74%
    .gallary-img
      width 100%
    .swiper-pagination
     color #fff
     bottom -1rem
</style>



