<template>
    <div>
        <HomeHeader></HomeHeader>
        <HomeSwiper></HomeSwiper>
        <HomeIcons></HomeIcons>
        <HomeRecommend></HomeRecommend>
        <HomeWeekend></HomeWeekend>
    </div>
</template>

<script>
import HomeHeader from '@/views/home/Header'
import HomeSwiper from '@/views/home/Swiper'
import HomeIcons from '@/views/home/Icons'
import HomeRecommend from '@/views/home/Recommend'
import HomeWeekend from '@/views/home/Weekend'
import {mapState} from 'vuex'

export default {
  name: 'home',
  data () {
    return {
      lastCity: ''
    }
  },
  components: {
    HomeHeader,
    HomeSwiper,
    HomeIcons,
    HomeRecommend,
    HomeWeekend
  },
  computed: {
    ...mapState({
      indexCity: state => state.indexModule.indexCity
    })
  },
  mounted () {
    this.lastCity = this.indexCity
    this.$store.dispatch('getIndexData')
  },
  activated () {
    if (this.lastCity !== this.indexCity) {
      this.lastCity = this.indexCity
      this.$store.dispatch('getIndexData')
    }
  }
}
</script>



