import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const HTTP = axios.create({
  baseURL: ' https://easy-mock.com/mock/5aefe7c7825f6e78c848a81a/travel',
  responseType: 'json'
})

let defaultCity = '广州'
try {
  if (localStorage.city) {
    defaultCity = localStorage.city
  }
} catch (e) {}

const cityModule = {
  state: {
    cities: {},
    hotCities: {},
    toLetterCity: ''
  },
  mutations: {
    setCity (state, playload) {
      const data = playload.data
      if (data.ret && data.data) {
        state.cities = data.data.cities
        state.hotCities = data.data.hotCities
      }
    },
    setToLetterCity (state, playload = '') {
      state.toLetterCity = playload.letter
    }
  },
  actions: {
    getCityData ({commit}) {
      HTTP.get('/city')
        .then((res) => {
          commit('setCity', res)
        })
        .catch((e) => {
          console.log(e)
        })
    }
  }
}

const indexModule = {
  state: {
    iconList: [],
    recommendList: [],
    swiperList: [],
    weekendList: [],
    indexCity: defaultCity
  },
  mutations: {
    setIndex (state, playload) {
      const data = playload.data
      if (data.ret && data.data) {
        state.iconList = data.data.iconList
        state.recommendList = data.data.recommendList
        state.swiperList = data.data.swiperList
        state.weekendList = data.data.weekendList
      }
    },
    setIndexCity (state, playload) {
      state.indexCity = playload
      try {
        localStorage.city = playload
      } catch (e) {}
    }
  },
  actions: {
    getIndexData ({ state, commit }) {
      HTTP.get('/index?city=' + state.indexCity)
        .then((res) => {
          commit('setIndex', res)
        })
        .catch((e) => {
          console.log(e)
        })
    }
  }
}

const store = new Vuex.Store({
  modules: {
    cityModule,
    indexModule
  }
})

export default store



