<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <img class="banner-img" :src="bannerImg" />
      <div class="banner-info">
        <div class="banner-number">
          <span class="iconfont banner-icon">&#xe63e;</span>
          {{gallaryImgs.length}}
        </div>
        <div class="banner-title">
          {{sightName}}
        </div>
      </div>
    </div>
    <fade-animation>
      <common-gallary :imgs="bannerImg" v-show="showGallry" @close="handleGallaryClose"></common-gallary>
    </fade-animation>
  </div>
</template>

<script>
import CommonGallary from '@/views/Common/Gallary'
import FadeAnimation from '@/views/Common/FadeAnimation'
export default {
  name: 'DetailBanner',
  props: {
    sightName: String,
    bannerImg: String,
    gallaryImgs: Array
  },
  data () {
    return {
      showGallry: false
    }
  },
  components: {
    CommonGallary,
    FadeAnimation
  },
  methods: {
    handleBannerClick () {
      this.showGallry = !this.showGallry
    },
    handleGallaryClose () {
      this.showGallry = !this.showGallry
    }
  }
}
</script>

<style lang='stylus' scoped>
  .banner
    position relative
    overflow hidden
    height 0
    padding-bottom 55%
    .banner-img
      width 100%
    .banner-info
      display flex
      flex-flow column nowrap
      position absolute
      left 0
      right 0
      bottom 0
      line-height .6rem
      color #fff
      background-image linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1))
      .banner-title
        flex 1
        font-size .32rem
        padding 0 .2rem
      .banner-number
        padding .06rem .2rem
        margin-left .2rem
        width .8rem
        height .32rem
        line-height .32rem
        border-radius .2rem
        font-size .24rem
        text-align center
        background rgba(0, 0, 0, .5)
        .banner-icon
          font-size .24rem
</style>



