<template>
  <div class="header">
    城市选择
    <div class="iconfont header-back" @click="backHandle">&#xe624;</div>
  </div>
</template>

<script>
export default {
  name: 'CityHeader',
  methods: {
    backHandle () {
      this.$router.back()
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
.header
  position relative
  height $headerHeight
  line-height $headerHeight
  text-align center
  color #fff
  overflow hidden
  background $bgColor
  font-size .32rem
  .header-back
    position absolute
    top 0
    left 0
    width .64rem
    font-size .4rem
    text-align center
</style>



