<template>
  <div class="icons">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(page, index) of pages" :key="index">
          <div class="icon" v-for="item of page" :key="item.id">
            <div class="icon-img">
              <img class="icon-img-content" :src="item.imgUrl" />
            </div>
            <p class="icon-desc">{{item.desc}}</p>
          </div>
        </swiper-slide>
      </swiper>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'HomeIcons',
  data () {
    return {
      swiperOption: {},
      pages: []
    }
  },
  computed: {
    ...mapState({
      list: state => state.indexModule.iconList
    })
  },
  watch: {
    list (newValue) {
      this.updatedList(newValue)
    }
  },
  methods: {
    updatedList (newValue) {
      let page = 0
      this.pages = []
      newValue.forEach((item, index) => {
        page = Math.floor(index / 8)
        if (!this.pages[page]) {
          this.pages.push([])
        }
        this.pages[page].push(item)
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/mixins.styl'@import '~styles/mixins.styl'
.icons >>> .swiper-container
  height 0
  padding-bottom 50%
.icons
  margin-top .1rem
  .icon
    position relative
    overflow hidden
    float left
    width 25%
    height 0
    padding-bottom 25%
    .icon-img
      position absolute
      top 0
      left 0
      right 0
      bottom .44rem
      box-sizing border-box
      padding .1rem
      .icon-img-content
        height 100%
        display block
        margin 0 auto
    .icon-desc
      position absolute
      left 0
      right 0
      bottom 0
      height .44rem
      line-height .44rem
      text-align center
      ellipsis()
</style>



