<template>
  <div>
    <div class="title">
      周末去哪儿
    </div>
    <ul>
      <li class="item border-bottom" v-for="item of list" :key="item.id">
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl" />
        </div>
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'HomeWeekend',
  computed: {
    ...mapState({
      list: state => state.indexModule.weekendList
    })
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/mixins.styl'
.title
  line-height .8rem
  background #eee
  text-indent .2rem
.item-img-wrapper
  overflow: hidden
  height 0
  padding-bottom 37.09%
  .item-img
    width 100%
.item-info
  padding: .1rem
  .item-title
    line-height .54rem
    font-size .32rem
    ellipsis()
    color #212121
  .item-desc
    line-height .4rem
    color #616161
    ellipsis()
</style>



